.header {
    display: flex;
  justify-content: space-between;
  align-items: center;
  height: 9.6rem;
  padding: 6rem 4.8rem;
  position: relative;

 background-color: transparent;
  }
  
  .logo {
    font-size: 5.5rem;
    color:#51cf66;
    font-weight: 700;
  }
  
  .header ul {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    align-items: baseline;
  }
  
  .header li {
    margin-left: 3rem;
  }
  
  .header a {
    text-decoration: none;
    font-size: 2.5rem;
    color: #333;
    font-weight: 500;
  }
  
  .header a:hover,
  .header a:active,
  .header a.active {
    color: #51cf66;
  }
  
  .badge {
    background-color:#51cf66;
    color: white;
    border-radius: 8px;
    padding: 0.8rem 1.5rem;
    margin-left: 0.8rem;
    font-size: 1.5rem;
    
  }